.champ-card .hero, .champ-card .banner {
  text-shadow: -1px -1px 0 #000,   1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000; }

.champ-card .hero {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; }

.champ-card {
  border: 3px solid black;
  border-radius: 3px;
  flex: 1;
  min-height: 8em;
  margin: 1em;
  display: flex;
  flex-direction: column;
  filter: drop-shadow(-5px -5px 5px rgba(0, 0, 0, 0.5));
  position: relative; }
  .champ-card.blank {
    border-style: solid;
    border-width: 3px;
    border-radius: 3px;
    border-image: linear-gradient(to bottom, #205257, #103435) 1 25%; }
  .champ-card.champ-cost-1 {
    border-color: lightslategrey; }
    .champ-card.champ-cost-1 .decoration {
      background-image: none; }
  .champ-card.champ-cost-2 {
    border-color: green; }
    .champ-card.champ-cost-2 .decoration {
      background-image: url(/img/profile-deco/2.png); }
  .champ-card.champ-cost-3 {
    border-color: navy; }
    .champ-card.champ-cost-3 .decoration {
      background-image: url(/img/profile-deco/3.png); }
  .champ-card.champ-cost-4 {
    border-color: magenta; }
    .champ-card.champ-cost-4 .decoration {
      background-image: url(/img/profile-deco/4.png); }
  .champ-card.champ-cost-5 {
    border-color: gold; }
    .champ-card.champ-cost-5 .decoration {
      background-image: url(/img/profile-deco/5.png); }
  .champ-card .decoration {
    position: absolute;
    top: 0%;
    width: 100%;
    height: 25%;
    z-index: 100;
    background-repeat: no-repeat;
    background-position: top center; }
  .champ-card .hero {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    text-align: left;
    background-color: black;
    color: white;
    padding: 0.5em; }
  .champ-card .banner .cost img {
    display: inline-block;
    height: 1em;
    vertical-align: middle;
    margin-right: 0.1em; }

.champ-card .banner {
  display: flex;
  padding: 0.5em;
  color: white; }

.champ-card .banner .name {
  flex: 1;
  text-align: left; }

.champ-card .banner .cost {
  justify-self: flex-end;
  text-align: end;
  width: 2em; }

.banner-cost-1 {
  background-color: lightslategrey; }

.banner-cost-2 {
  background-color: green; }

.banner-cost-3 {
  background-color: navy; }

.banner-cost-4 {
  background-color: magenta; }

.banner-cost-5 {
  background-color: gold; }
