.champion {
  text-shadow: -1px -1px 0 #000,   1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000; }

.champion {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; }

.bench {
  padding: 0.5em;
  display: flex; }

.bench-slot {
  border: 2px solid white;
  border-radius: 5px;
  flex: 1;
  height: 5em;
  margin: 0.1em; }
  .bench-slot:not(.selected) {
    border-style: solid;
    border-width: 3px;
    border-radius: 3px;
    border-image: linear-gradient(to bottom, #205257, #103435) 1 25%; }
  .bench-slot.selected {
    border-style: solid;
    border-width: 3px;
    border-radius: 3px;
    border-image: linear-gradient(to bottom, #816C34, #816C34) 1 25%; }

.champion {
  background-color: black;
  color: white;
  height: 100%;
  text-align: center; }
  .champion .champion-tier {
    font-size: 1.5em; }
