.draft .above-bar, .draft .refresh-panel, .draft .xp-panel {
  text-shadow: -1px -1px 0 #000,   1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000; }

.draft .draft-lock .lock {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat; }

.draft .above-bar {
  border-style: solid;
  border-width: 6px;
  border-radius: 3px;
  border-image: linear-gradient(to bottom, #816C34, #816C34) 1 25%; }

.draft .draft-area {
  border-style: solid;
  border-width: 6px;
  border-radius: 3px;
  border-image: linear-gradient(to bottom, #816C34, #725C31) 1 25%; }

.draft {
  padding: 0.5em;
  display: flex;
  margin-top: 3em;
  min-height: 12.5em;
  position: relative; }
  .draft .draft-area {
    border-radius: 3px;
    position: relative;
    background-color: black; }
  .draft .draft-sell {
    margin: 0.5em;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0; }
  .draft .above-bar {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, -100%);
    border-bottom: none;
    border-radius: 3px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    background-color: black;
    color: white; }
  .draft .draft-sidebar {
    flex: 1;
    max-width: 12em;
    display: flex;
    flex-direction: column;
    border-right: none;
    border-bottom-left-radius: 3px;
    border-top-left-radius: 3px;
    border-bottom-right-radius: 0px;
    border-top-right-radius: 0px; }
  .draft .draft-sidebar-items {
    display: flex;
    flex-direction: column; }
  .draft .sidebar-item {
    flex: 1;
    border: 2px solid goldenrod;
    margin: 0.5em;
    padding: 0.1em;
    background-color: black; }
    .draft .sidebar-item.refresh {
      border-color: #857347; }
    .draft .sidebar-item.xp {
      border-color: #3A7393; }
    .draft .sidebar-item .content {
      flex: 1; }
    .draft .sidebar-item .icon {
      width: 4em;
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat; }
  .draft .draft-lock {
    padding: 0.5em;
    padding-bottom: 0; }
    .draft .draft-lock .lock {
      height: 2em;
      width: 4em;
      border-style: solid;
      border-width: 3px;
      border-radius: 3px;
      border-image: linear-gradient(to bottom, #205257, #103435) 1 25%;
      background-image: url("/img/lock-open.png"); }
      .draft .draft-lock .lock.closed {
        background-image: url("/img/lock-closed.png"); }
  .draft .draft-main {
    flex: 4;
    border-left: none;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0; }
  .draft .draft-gold-bar {
    padding: 0.5em;
    padding-bottom: 0; }
    .draft .draft-gold-bar .draft-gold {
      padding: 0.5em 1em;
      border-style: solid;
      border-width: 3px;
      border-radius: 3px;
      border-image: linear-gradient(to bottom, #205257, #103435) 1 25%; }
  .draft .hand {
    display: flex;
    z-index: 1;
    height: 100%; }
  .draft .refresh-panel {
    color: white;
    background-color: #857347;
    padding: 0.5em;
    display: flex; }
  .draft .xp-panel {
    display: flex;
    flex-direction: column;
    position: relative;
    background: #3A7393;
    color: white;
    padding: 0.5em; }
    .draft .xp-panel .details {
      display: flex; }
    .draft .xp-panel .level {
      position: absolute;
      bottom: 0;
      right: 0;
      height: 2em;
      width: 2em;
      padding: 0.5em;
      background-color: rgba(255, 255, 255, 0.5);
      vertical-align: middle;
      text-align: center; }
    .draft .xp-panel .xp-progress {
      width: 100%;
      height: 1em;
      flex: 1;
      display: flex; }
      .draft .xp-panel .xp-progress .xp-progress-bar {
        flex: 1;
        display: flex;
        flex-direction: column-reverse;
        padding: 0 2em 0 0.5em; }
        .draft .xp-panel .xp-progress .xp-progress-bar .bar {
          background-color: black;
          height: 1em;
          position: relative;
          overflow: hidden; }
        .draft .xp-panel .xp-progress .xp-progress-bar .fill {
          background-color: lightblue;
          height: 100%;
          width: 100%;
          transition: transform 0.2s; }

i.trash {
  display: inline-block;
  height: 1em;
  width: 1em;
  margin-right: 0.1em;
  background-image: url("/img/trash.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain; }
